
import { DOCUMENT } from '@angular/common';

import { Component, OnInit, Inject, Renderer2, AfterViewInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { WOW } from 'wowjs/dist/wow.min';

import { AngularFireAuth } from  "@angular/fire/auth";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements AfterViewInit  {

  user: firebase.User;

  isNotHomeRoute:boolean = false;

  

  constructor(
    public router: Router, 
    public afAuth: AngularFireAuth,
    @Inject(DOCUMENT) document, 
    r: Renderer2) { 

    let bodyClassList:string = 'home';

    router.events.subscribe( (event: any) => {

      if (event instanceof NavigationEnd) {

          let path = this.router.url;
          let name = path.split('/');
          r.removeClass(document.body,bodyClassList);
          r.addClass(document.body, name[1]);

           if(this.router.url == '/home') {
             this.isNotHomeRoute = false;
           }
           else {
             this.isNotHomeRoute = true;
           }
       }

    });

  }

  ngAfterViewInit() {
    
    
  }

}

