
import { Directive, OnInit, ElementRef, Renderer2, Output, NgZone, EventEmitter, HostListener, OnChanges, SimpleChanges } from '@angular/core';
import { NgModel, NgForm, ControlContainer } from '@angular/forms';

import { MapsAPILoader } from '@agm/core';

declare var google: any;
declare var $:JQueryStatic;

@Directive({
  selector: '[AwakeClass]'
})

export class AwakeClassDirective {

  constructor(private element: ElementRef, private renderer: Renderer2) { }

  @HostListener('window:scroll', ['$event']) 
  	onWindowScroll() {
  		if(window.scrollY > 100) {
  			this.addClass('awake');
  		}
  		else {
  			this.removeClass('awake');
  		}
   }

   addClass(className: string) {
      this.renderer.addClass(this.element.nativeElement, className);
   }

   removeClass(className: string) {
       this.renderer.removeClass(this.element.nativeElement,className);
   }

}

@Directive({
  selector: '.select',
  providers : [NgModel]
})

export class SelectDirective implements OnInit {

  @Output() ngModelChange = new EventEmitter();

  constructor(
    private element: ElementRef, 
    private renderer: Renderer2,
    private ngModel : NgModel
    ) { 

    
  }
  
  @HostListener("click", ["$event"])
   onClick(event) {
     
      this.enableSelect();


   }

   enableSelect() {

     var el = this.element.nativeElement;
      var arrowElement = el.nextSibling;
      var selectDropdown = arrowElement.nextSibling;

      const arrowClass = arrowElement.classList.contains('up');
      const selectClass = selectDropdown.classList.contains('show');

      if(arrowClass) {
        this.renderer.removeClass(arrowElement,"up");
      }
      else {
        this.renderer.addClass(arrowElement,"up");
      }
       
      if(selectClass) {
        this.renderer.removeClass(selectDropdown,"show");
      }
      else {
        this.renderer.addClass(selectDropdown,"show");
      }


   }


   @HostListener('document:click', ['$event']) 
    onDocumentClick(event) {

      var el = this.element.nativeElement;
      var arrowElement = el.nextSibling;

      const arrowClass = arrowElement.classList.contains('up');

      if (!el.contains(event.target)) {

        if(arrowClass) {
          this.enableSelect();
        }
       
      }
     
   }

   ngOnInit() {
    
   }

}


