@Directive({
  selector: '.location-search',
  providers : [NgModel]
})

export class LocationSearchDirective implements OnInit {

  @Output() ngModelChange:EventEmitter<any> = new EventEmitter();

  constructor(
    private element: ElementRef,
    private ngModel : NgModel,
    private mapsAPILoader: MapsAPILoader, private ngZone: NgZone
  ) {}
  


  ngOnInit() {

    this.mapsAPILoader.load().then(
      () => {

       let options = {
        types: ['(cities)'],
        componentRestrictions: {country: "us"}
       };

       let autocomplete = new google.maps.places.Autocomplete(this.element.nativeElement, options);

        autocomplete.addListener("place_changed", () => {
          this.ngZone.run(() => {
           let place: google.maps.places.PlaceResult = autocomplete.getPlace();

           if(place.address_components)
             
             var splitname = place.formatted_address.split(',');
             this.ngModelChange.emit(splitname[0] +','+splitname[1]);
            

           if(place.geometry === undefined || place.geometry === null ){
            return;
           }
    

          });

        });


        document.addEventListener("DOMNodeInserted", (event) => {

          let target = $(event.target);
          if(target.hasClass('pac-item')){
            target.html(target.html().replace(/, USA<\/span>$/, "</span>"))
          }

        });

      }
    );

    


  }

}



