import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { Observable } from "rxjs";
import { FormsModule } from '@angular/forms';

import { AuthService } from '../.././services/auth.service';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.scss']
})
export class SignupComponent implements OnInit {

  userEmailId:string = '';
  userPassword:string = '';
  userName:string = '';
  userTerms:boolean = false;

  isEmailAlreadyExists:boolean = false;

  constructor(private authService: AuthService) { }

  loginGoogle() {
    this.authService.loginGoogle();
  }

  clearErrors() {
    this.isEmailAlreadyExists = false;
  }

  signupEmail(email, password, username){
    this.authService.signupEmail(email, password, username)
      .then((result) => {
        return result.user.updateProfile({
          displayName: username
        })
      }).catch((error) => {
        
        var code = error.code;
        if(code == "auth/email-already-in-use") {
            this.isEmailAlreadyExists = true;
        }

      })
  }

  ngOnInit() {
  }

}
