import { Component, OnInit, HostListener, EventEmitter, ViewEncapsulation } from '@angular/core';
import { Router, Event, NavigationEnd, ActivatedRoute } from '@angular/router';

import { CandidateDetailSheetComponent } from './../shared/candidate-detail-sheet/candidate-detail-sheet.component';

import { MatDialog } from '@angular/material';
import { MatDialogRef } from "@angular/material";

import { SharedService } from '../.././services/shared.service';
import { CandidateProfileService } from '../.././api/services/candidate-profile.service';

import { ActivateDeActivate } from '../.././api/models/activate-de-activate';
import { ConsultancyService } from '../.././api/services/consultancy.service';

import * as _ from 'underscore';

@Component({
  selector: 'app-my-hotlist',
  templateUrl: './my-hotlist.component.html',
  styleUrls: ['./my-hotlist.component.scss'],
  providers: [
    { provide: MatDialogRef, useValue: {} },
  ]
})
export class MyHotlistComponent implements OnInit {
  
  isJobLoaded:boolean = false;
  isJobAvailable:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any = 9;
  itemLimit:any = 10;
  totalItems:any;

  skillSetData:any;
  hotListData:any;
  isActiveCandidate:any;
  totalExp:any;
  visaList:any;

  isMobile:boolean = false;

  selectedCandidateId:number = -1;
  selectedCandidateDetails:any;

  isActive:ActivateDeActivate = {};

  candidateDetailsChanged: EventEmitter<any> = new EventEmitter();

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    public dialog: MatDialog,
    private sharedService:SharedService,
    private candidateProfileService: CandidateProfileService,
    private consultancyService : ConsultancyService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;

  }

  getSkills(id){

   if(this.skillSetData != null) {
     this.skillSetData.find((res) => {
       if(res.id == id && res != null){
         return 1;
       }
     });
   }
   
  }

  getParams(event){
    this.isActiveCandidate = event.active;
    this.totalExp = event.experience;
  }

  showCandidateDetaislModal(){

    const jobDialogRef = this.dialog.open(CandidateDetailSheetComponent, {
        panelClass: 'material',
        disableClose: true,
        data: this.selectedCandidateDetails
    });

  }

  @HostListener('window:resize', ['$event'])
    onResize(event){

      if(event.target.innerWidth <= 991)
      this.isMobile = true;
      else
      this.isMobile = false;

  }

  isSelected(id){
    return this.selectedCandidateId == id ? 'selected' : '';
  }

  setActive(item){

    this.isActive = {
      id: item.id,
      active: !item.active
    }

    this.candidateProfileService.ActDeActJobOpeningAsync(this.isActive).subscribe( (res : any) => {

    })
  }

  showCandidateDescription(candidate){
    this.selectedCandidateId = candidate.id;
    this.selectedCandidateDetails = candidate;

    this.candidateDetailsChanged.emit(this.selectedCandidateDetails);
  }

  getVisaList(id){
    var name;
    _.some(this.visaList, (item) => {
      if(item.id == id)
        name = item.name;
    });
    return name;
  }

  deleteIcon(){
    
  }

  ngOnInit() {

    if(window.innerWidth <= 991)
      this.isMobile = true;
    else
      this.isMobile = false;


    this.candidateProfileService.GetCandidateProfileByConsultancyUserIdAsync().subscribe((res:any) => {
          
      console.log(res);
      this.hotListData = res;

      if(this.hotListData.length>0){
        
        this.isJobAvailable = true;
        this.isJobLoaded = true;

        if(!this.isMobile){
          this.selectedCandidateId = this.hotListData[0].id;
          this.selectedCandidateDetails = this.hotListData[0];
          this.candidateDetailsChanged.emit(this.selectedCandidateDetails);
        }

        this.totalItems = this.hotListData.length;

      }
      else {
        this.isJobAvailable = false;
        this.isJobLoaded = true;

        this.totalItems = 0;
      }

      

      this.isJobLoaded = true;

      if(this.totalItems>0)
        this.isJobAvailable = true;
      else
        this.isJobAvailable = false;

    });

    this.candidateDetailsChanged.subscribe( jobDetails => {

      if(this.isMobile){
        this.showCandidateDetaislModal();
      }

    })

    // fetch related data from json
    this.sharedService.getJsonData().subscribe((res : any)=>{

      this.visaList = res.visaList;

    },
    error => {
      console.log(error);
    }
    );

    /*this.candidateProfileService.GetCandidateProfileByIdAsync(31).subscribe((res : any) => {
      console.log(res);
    },
    error => {
      console.log(error);
    }
    );*/

    this.sharedService.skillsetdatacast.subscribe( res => {
      this.skillSetData = res;
    });



  }

}
