import { Component, OnInit, Input, Inject, Optional, HostListener, ViewEncapsulation } from '@angular/core';
import { Router, Event, NavigationEnd, ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material';
import { MatDialogRef } from "@angular/material";
import { MAT_DIALOG_DATA } from '@angular/material';

import { ConfirmApplyJobComponent } from '.././confirm-apply-job/confirm-apply-job.component';

import * as _ from 'underscore';

@Component({
  selector: 'app-job-detail-sheet',
  templateUrl: './job-detail-sheet.component.html',
  styleUrls: ['./job-detail-sheet.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class JobDetailSheetComponent implements OnInit {

  @Input() selectedJobDetails:any;
  @Input() isJobName:boolean;

  isMobile:boolean = false;

  constructor(
     @Optional() @Inject(MAT_DIALOG_DATA) public modalJobDetails: any,
     public dialog: MatDialog,
     private jobDialogRef: MatDialogRef<JobDetailSheetComponent>,
  	 private route: ActivatedRoute
  ) { }

  isModal(){
    return this.modalJobDetails != null ? true : false;
  }

  dialogClose(){
    if(!_.isEmpty(this.jobDialogRef))
    this.jobDialogRef.close();
  }

  showApplyJobConfirmModal(){

    this.dialogClose();

    const applyJobDialogRef = this.dialog.open(ConfirmApplyJobComponent, {
        panelClass: 'material',
        disableClose: true,
        data: this.selectedJobDetails
    });

  }

  getVisaDetails(visa){
    if(visa != null)
    return visa.split(',');
    else
      return [];
  }

  @HostListener('window:resize', ['$event'])
    onResize(event){

      if(event.target.innerWidth <= 991)
      this.isMobile = true;
    else
      this.isMobile = false;

     if(!this.isMobile){
        this.dialogClose();
      }
  }

  ngOnInit() {

  	if(this.modalJobDetails != null){
      this.selectedJobDetails = this.modalJobDetails;
    }

    if(window.innerWidth <= 991)
      this.isMobile = true;
    else
      this.isMobile = false;

  }

}
