import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { SharedService } from '../.././services/shared.service';
import { CandidateProfileService } from '../.././api/services/candidate-profile.service';
import { CandidateProfile } from '../.././api/models/candidate-profile';

import * as _ from 'underscore';

@Component({
  selector: 'app-add-candidate',
  templateUrl: './add-candidate.component.html',
  styleUrls: ['./add-candidate.component.scss']
})
export class AddCandidateComponent implements OnInit {

  candidate:any;
   
  candidateName:any;
  candidateSkill:any;

  selectSkillList:any;
  selectDomainList:any;  
  selectBillingList:any;
  selectAvailabilityList:any;
  selectRelocationList:any;
  selectVisaList:any;

  candidateRelocation = [];
  candidateDomainSkill = [];
  candidateVisa = [];
 
  candidatePosition:any;
  candidateBillingRange:any;
  candidateAvailability:any;
  candidateLocation:any;
  candidatePrimarySkill:any;
  candidateSecondarySkill:any;

  candidateDetails : CandidateProfile;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
  	private sharedService:SharedService,
    private candidateProfileService: CandidateProfileService
  ) { }

  onBillingListChange(event){

  }

  onAvailabilityListChange(event){
    
  }

  onRelocationChange(event){
    
  }

  onVisaChange(event){
    
  }

  onSkillSetChange(event){

  }

  onDomainChange(event){
    
  }

  submitAddCandidateForm(form){

    this.candidateDetails = {
      candidateName : this.candidate.candidateName,
      title : this.candidatePosition,
      totalExp : this.candidate.totalExp
    }

    this.candidateProfileService.AddCandidateProfileAsync(this.candidateDetails).subscribe((res : any) => {
      console.log(res);
    });

  }

  resolved(captchaResponse: string) {
        console.log(`Resolved captcha with response: ${captchaResponse}`);
  }

  ngOnInit() {

    this.candidate = {};

  	// fetch related data from json
    this.sharedService.getJsonData().subscribe((res : any)=>{

      this.selectDomainList = res.domainList;
      this.selectBillingList = res.billingList;
      this.selectAvailabilityList = res.availabilityList;
      this.selectRelocationList = res.relocationList
      this.selectVisaList = res.visaList;
    },
    error => {
      console.log(error);
    }
    );

    this.sharedService.skillsetdatacast.subscribe(data => {
      this.selectSkillList = data;
    })

    if(this.route.params['value'].id != undefined){
        
      this.candidateProfileService.GetCandidateProfileByIdAsync(this.route.params['value'].id).subscribe((res:any) => {
        
        console.log(res);

        if(!_.isEmpty(res)){
          this.candidate = res;

          this.candidateLocation = this.candidate.city+', '+this.candidate.state;

        }

        

      });

    }

    

  }

}
