import { Component, OnInit } from '@angular/core';

import { Router, Event, NavigationEnd } from '@angular/router';

import { AuthService } from '../.././services/auth.service';
import { MappingService } from '../.././api/services/mapping.service';
import { SharedService } from '../.././services/shared.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  isMenuExpanded:boolean = false;

  isNotHomeRoute:boolean = false;

  isJobMiniFormSubmitted:boolean = false;

  //user: firebase.User;
  user:any;

  userName:any;

  constructor(
    public router: Router, 
    private authService: AuthService,
    private sharedService:SharedService,
    private mappingService: MappingService,
    private cookieService:CookieService) { 

    router.events.subscribe( (event: Event) => {

      if (event instanceof NavigationEnd) {
           if(this.router.url == '/home') {
             this.isNotHomeRoute = false;
           }
           else {
             this.isNotHomeRoute = true;
           }
           this.isMenuExpanded = false;
       }



    });

  }

  showMenuMini() {
  	this.isMenuExpanded = !this.isMenuExpanded;
  }


  logout() {
    this.authService.logout();
  }

  

  ngOnInit() {

    this.authService.userdetailscast.subscribe(user => {
      this.user = user;
    });

    //check the session on page refresh and load all data 
    if(this.cookieService.get('token') != '') {
      this.authService.setUserDetails({'name':this.cookieService.get('username')});
    }


    this.mappingService.GetLocation().subscribe((res : any)=>{
      
      this.sharedService.setLocationData(res);

      this.mappingService.GetSkills().subscribe((res : any) => {
       this.sharedService.setSkillData(res);
      },
      error => {
         console.log(error);
      })

    },
    error => {
      console.log(error);
    })

  }

}
