import { Injectable } from '@angular/core';
import { auth } from  'firebase/app';
import { BehaviorSubject } from 'rxjs';
import { Router} from '@angular/router';

import { HttpClient, HttpHeaders, HttpParams  } from '@angular/common/http';

import { AngularFireAuth } from  "@angular/fire/auth";

import { CookieService } from 'ngx-cookie-service';

const httpOptions = {
    headers: new HttpHeaders({ 
      'Content-Type' : 'application/json'
    })
};

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private userDetails = new BehaviorSubject<any>(null);
  userdetailscast = this.userDetails.asObservable();

  constructor(
    private cookieService:CookieService, 
    private http: HttpClient, 
  	public afAuth: AngularFireAuth,
  	public router: Router
  ) { }

  setUserDetails(details){
    this.userDetails.next(details);
  }

  getUserDetails(){
    return this.userDetails.value;
  }

  loginGoogle() {
  	console.log('login by google login');
  	this.afAuth.auth.signInWithPopup(new auth.GoogleAuthProvider());
  }

  logout() {
  	//this.afAuth.auth.signOut();
    this.cookieService.delete('token');
    this.cookieService.delete('username');
    this.userDetails.next(null);
    this.router.navigate(['home']);
  }

  getLoggeduser() {
  	return this.afAuth.authState;
  }

  signupEmail(email, password, username) {
    return this.afAuth.auth.createUserWithEmailAndPassword(email, password)
      
  }

  // Sign in with email/password
  /*logInEmail(email, password) {
    return this.afAuth.auth.signInWithEmailAndPassword(email, password);    
  }*/


}


