import { Component, OnInit, Input, Output, EventEmitter, ElementRef, Renderer } from '@angular/core';

import { ControlContainer, NgForm } from '@angular/forms';
import { SharedService } from '../../.././services/shared.service';


@Component({
  selector: 'app-multi-select-field',
  templateUrl: './multi-select-field.component.html',
  styleUrls: ['./multi-select-field.component.scss'],
  viewProviders: [ { provide: ControlContainer, useExisting: NgForm } ]
})
export class MultiSelectFieldComponent implements OnInit {

  @Input() fieldName:string;
  @Input() fieldText:string;
  @Input() fieldPlaceholder:string;
  @Input() fieldModel:any;
  @Input() fieldRequired:string;
  @Input() fieldList:any;

  selectedItem:any;

  @Output() inputChange = new EventEmitter();

  constructor(
     private sharedService:SharedService,
     private element: ElementRef, private renderer1: Renderer
  ) { }

  isFieldRequired(){
    return this.fieldRequired == "required" ? true : false;
  }

  isSelectedItem(item, model) {
      
    if(model != null && model.length != 0) {
      var i = model.length;
      while (i--) {
         if (model[i] === item) {
             return 'selected';
         }
      }
      return '';
    }
    else
      return ''; 

  }

  isModelNotEmpty(model) {
    return ( model != null && model.length > 0 && Array.isArray(model) ) ? 'no-text' : '';
  }

  getSelectedItem(item, event) {
    this.pushSelectedItems(this.fieldModel, item.name);
    this.selectedItem = item.name;
    this.renderer1.setElementClass(event.target,'selected',true);

    setTimeout(()=>{
      var inputElement = document.getElementById(this.removeSpace(this.fieldName));
      inputElement.style.height = inputElement.nextElementSibling.nextElementSibling.nextElementSibling.clientHeight+10+'px';
    }, 0);
    

    this.inputChange.emit(this.fieldModel);
  }

  clearList(){
    this.fieldModel = [];
    this.selectedItem = null;
  }

  deleteBadgeItem(item, badges, event){
    const badgeIndex: number = badges.indexOf(item);
    if (badgeIndex !== -1) {
      badges.splice(badgeIndex, 1);
      document.querySelector('#userList'+ this.removeSpace(item)).classList.remove('selected');
      if(badges.length==0) {
        this.clearList();
      }
    }

    setTimeout(() => {
      var inputElement = document.getElementById(this.removeSpace(this.fieldName));
      inputElement.style.height = inputElement.nextElementSibling.nextElementSibling.nextElementSibling.clientHeight+10+'px';
    }, 0);

    
   
  }

  removeSpace(item){
    var item = item.name || item;
    return item.replace(/[^A-Z0-9]/ig, "_");
  }

  pushSelectedItems(model, item) {
    if(model.indexOf(item) === -1){
        model.push(item);
    }
  }

  itemsStringify(){
    return this.fieldModel.length;
  }

  ngOnInit() {

    

  }

}
