import { Component, OnInit, HostListener, EventEmitter, ViewEncapsulation } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material';
import { MatDialogRef } from "@angular/material";

import { SharedService } from '../.././services/shared.service';

@Component({
  selector: 'app-search-hotlist',
  templateUrl: './search-hotlist.component.html',
  styleUrls: ['./search-hotlist.component.scss']
})
export class SearchHotlistComponent implements OnInit {

  skillName:any;
  locationName:any;

  skillId:number;
  locationId:number;

  isHotListLoaded:boolean = false;
  isHotListAvailable:boolean = false;
  isParamsAvailable:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any = 9;
  itemLimit:any = 10;
  totalItems:any;

  hotListData:any;

  isActiveCandidate:any;

  totalExp:any;


  constructor(
	  private router: Router,
	  private route: ActivatedRoute,
	  public dialog: MatDialog,
	  private sharedService:SharedService
  ) { 


  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;

  }

  getSkills(skills){
    return skills.split(',');
  }

  getParams(event){
    this.isActiveCandidate = event.active;
    this.totalExp = event.experience;
  }

  ngOnInit() {

    this.isParamsAvailable = true;
    
  	this.sharedService.getJsonData().subscribe((res : any) => {


      this.hotListData = res.myHotList;
      console.log(this.hotListData);
      this.totalItems = this.hotListData.length;

      this.isHotListLoaded = true;

      if(this.totalItems>0)
        this.isHotListAvailable = true;
      else
        this.isHotListAvailable = false;

    },
    error => {
      console.log(error);
    }
    );

  }

}
