import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { SharedService } from '../../.././services/shared.service';

import * as _ from 'underscore';

export interface visaList {
   name : string;
   checked : boolean;
}

@Component({
  selector: 'app-filter-box',
  templateUrl: './filter-box.component.html',
  styleUrls: ['./filter-box.component.scss']
})


export class FilterBoxComponent implements OnInit {

  jobPostedDate:any;
  jobExperience:any;

  isActive:any = null;
  isRelocation:any = null;

  selectJobVisaList : visaList;

  selectedVisaFilter = [];

  selectAllVisa:boolean = false;

  @Input() isDatePosted?: string;
  @Input() isExperience?: string;
  @Input() isActiveCandidates?: string;
  @Input() isRelocationCandidate?: string;
  @Input() isVisa?: string;

  @Output() public outputParams = new EventEmitter();

  constructor(
    private sharedService:SharedService
  ) { }

  isDatePostedAvailable(){
    return this.isDatePosted == 'yes' ? true : false;
  }

  isExperienceAvailable(){
    return this.isExperience == 'yes' ? true : false;
  }

  isActiveCandidatesAvailable(){
    return this.isActiveCandidates == 'yes' ? true : false;
  }

  isRelocationAvailable(){
    return this.isRelocationCandidate == 'yes' ? true : false;
  }

  isVisaAvailable(){
    return this.isVisa == 'yes' ? true : false;
  }

  isFilterSelected(model){
    return model != null ? 'active' : '';
  }

  getExperience(){
    this.outputParams.emit({'experience':this.jobExperience})
  }

  getActiveCandidate(){
    if(!this.isActive)
      this.isActive = null;
    this.outputParams.emit({'active':this.isActive})
  }

  getRelocationCandidate(){
    if(!this.isRelocation)
      this.isRelocation = null;
    this.outputParams.emit({'relocation':this.isRelocation})
  }

  getSelectedVisa(item){
    if(item.checked)
      this.selectedVisaFilter.push(item.name)
    else
      this.selectedVisaFilter = _.without(this.selectedVisaFilter, item.name);

    this.outputParams.emit({'visa':this.selectedVisaFilter})

  }

  getAllVisaSelected(){
    if(this.selectAllVisa){
      _.each(this.selectJobVisaList, (item)=>{
        this.selectedVisaFilter.push(item.name)
        item.checked = true;
      })
    }
    else{
      _.each(this.selectJobVisaList, (item)=>{
        item.checked = false;
      })
      this.selectedVisaFilter = [];
    }

    this.outputParams.emit({'visa':this.selectedVisaFilter})

  }


  clearFilter(type){
    if(type == 'date-posted'){
      this.jobPostedDate = null;
      this.outputParams.emit({'experience':this.jobExperience})
    }
    else if(type == 'years-exp'){
      this.jobExperience = null;
      this.outputParams.emit({'experience':this.jobExperience})
    }
    else if(type == 'visa'){
      this.selectJobVisaList = _.map(this.selectJobVisaList, function (el) {
        return { name: el.name, checked: false };
      });
      this.selectedVisaFilter = [];
      this.selectAllVisa = false;
      this.outputParams.emit({'visa':this.selectedVisaFilter})
    }

  }

  ngOnInit() {


    // fetch related data from json
    this.sharedService.getJsonData().subscribe((res : any)=>{

      var array = res.visaPostJobList;

      this.selectJobVisaList = _.map(array, function (el) {
        return { name: el, checked: false };
      });


    },
    error => {
      console.log(error);
    }
    );

  }

}
