import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { Observable } from "rxjs";
import { FormsModule } from '@angular/forms';


import { Login } from '../.././api/models/login';
import { TokenService } from '../.././api/services/token.service';

import { AuthService } from '../.././services/auth.service';
import { CookieService } from 'ngx-cookie-service';

import { CandidateProfileService } from '../.././api/services/candidate-profile.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {


  userName:string = '';
  userPassword:string = '';
  isInvalidPassword:boolean = false;
  isFormSubmitted:boolean = true;

  loginData:Login = {};

  constructor(
    public router: Router, 
    private authService: AuthService,
    private tokenService: TokenService,
    private candidateProfileService: CandidateProfileService,
    private cookieService:CookieService
    ) { }  

  user: firebase.User;


  loginGoogle() {
    this.authService.loginGoogle();
  }

  loginEmail(){

      this.loginData = {
        'username':this.userName,
        'password':this.userPassword
      }


      this.tokenService.CreateToken(this.loginData).subscribe((res:any) => {

        //store the tooken in cookie
        this.cookieService.set('token', res.token);

        this.cookieService.set('username', this.userName);
        this.authService.setUserDetails({'name':this.cookieService.get('username')});
        this.router.navigate(['dashboard']);


      })
  }

  clearErrors() {
    this.isInvalidPassword = false;
  }

  submitLoginForm() {

    this.isFormSubmitted = false;

  }

  ngOnInit() {



  }

}
