import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './components/login/login.component';
import { SignupComponent } from './components/signup/signup.component';
import { HowitworksComponent } from './components/howitworks/howitworks.component';
import { PostjobsComponent } from './components/postjobs/postjobs.component';
import { SearchjobsComponent } from './components/searchjobs/searchjobs.component';
import { HomeComponent } from './components/home/home.component';
import { DemoComponent } from './components/demo/demo.component';
import { AboutusComponent } from './components/aboutus/aboutus.component';
import { TermsComponent } from './components/terms/terms.component';
import { PrivacyComponent } from './components/privacy/privacy.component';
import { ContactusComponent } from './components/contactus/contactus.component';
import { FaqsComponent } from './components/faqs/faqs.component';
import { ForgotpasswordComponent } from './components/forgotpassword/forgotpassword.component';
import { EmailstatusComponent } from './components/emailstatus/emailstatus.component';
import { SearchHotlistComponent } from './components/search-hotlist/search-hotlist.component';
import { JobDetailSheetComponent } from './components/shared/job-detail-sheet/job-detail-sheet.component';
import { MyHotlistComponent } from './components/my-hotlist/my-hotlist.component';
import { AddCandidateComponent } from './components/add-candidate/add-candidate.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { MyAccountComponent } from './components/my-account/my-account.component';
import { ProfileComponent } from './components/profile/profile.component';

const routes: Routes = [
	{ path: '', redirectTo: '/home', pathMatch: 'full' },
	{ path: 'home', component: HomeComponent },
	{ path: 'postjobs', component: PostjobsComponent },

	{ path: 'jobsearch', component: SearchjobsComponent },
	{ path: 'jobsearch/:searchparams', component: SearchjobsComponent },

	{ path: 'hotlist', component: SearchHotlistComponent },
	{ path: 'hotlist/:searchParams', component: SearchHotlistComponent },

	{ path: 'dashboard', component: DashboardComponent },
	{ path: 'myaccount', component: MyAccountComponent },
	{ path: 'profile', component: ProfileComponent },
	{ path: 'profile/:username', component: ProfileComponent },

	{ path: 'addcandidate', component: AddCandidateComponent },
	{ path: 'editcandidate/:id', component: AddCandidateComponent },

	{ path: 'jobdetails/:id', component: JobDetailSheetComponent },
	{ path: 'myhotlist', component: MyHotlistComponent },
	{ path: 'howitworks', component: HowitworksComponent },
	{ path: 'demo', component: DemoComponent },
	{ path: 'login', component: LoginComponent },
	{ path: 'signup', component: SignupComponent },
	{ path: 'aboutus', component: AboutusComponent },
	{ path: 'terms', component: TermsComponent },
	{ path: 'privacy', component: PrivacyComponent },
	{ path: 'contactus', component: ContactusComponent },
	{ path: 'faqs', component: FaqsComponent },
	{ path: 'forgotpassword', component: ForgotpasswordComponent },
	{ path: 'emailstatus', component: EmailstatusComponent },
	{ path: '**', component: HomeComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
