import { Component, OnInit, HostListener, EventEmitter, ViewEncapsulation } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material';
import { MatDialogRef } from "@angular/material";

import { JobOpeningSearch } from '../.././api/models/job-opening-search';
import { JobOpeningContollerService } from '../.././api/services/job-opening-contoller.service';

import { JobDetailSheetComponent } from '.././shared/job-detail-sheet/job-detail-sheet.component';

import { SharedService } from '../.././services/shared.service';

import * as _ from 'underscore';

@Component({
  selector: 'app-searchjobs',
  templateUrl: './searchjobs.component.html',
  styleUrls: ['./searchjobs.component.scss'],
  providers: [
    { provide: MatDialogRef, useValue: {} },
  ]
})
export class SearchjobsComponent implements OnInit {

  skillName:any;
  locationName:any;

  skillId:number;
  locationId:number;

  skillList:any;
  locationList:any;

  searchList:JobOpeningSearch = {};

  jobData:any;
  jobCount:any;

  selectedJobId:number = -1;
  selectedJobDetails:any;

  isJobLoaded:boolean = true;
  isJobAvailable:boolean = false;
  isParamsAvailable:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any = 9;
  itemLimit:any = 10;
  totalItems:any;

  isMobile:boolean = false;
  isJobName:boolean = true;

  filterParam:any;

  jobDetailsChanged: EventEmitter<any> = new EventEmitter();

  constructor(
  private router: Router,
  private route: ActivatedRoute,
  public dialog: MatDialog,
  private jobOpeningService: JobOpeningContollerService,
  private sharedService:SharedService
  ) { 

    router.events.subscribe((event: any) => {
      if (event instanceof NavigationEnd) {
        if (this.router.url.indexOf('jobsearch') > -1) {
            
            this.isParamsAvailable = true;

             this.sharedService.skillsetdatacast.subscribe(data => {
               this.skillList = data;
               this.sharedService.locationsetdatacast.subscribe(data => {
                  this.locationList = data;
                  this.showJobsData(this.route.params['value'].searchparams);
               });
             });        
        }
      }
    });

  }

  showJobsData(params){

    var self = this;
    this.isJobLoaded = false;

    if(params){

      var data = params.split('&');
      this.skillName = data[0];
      this.locationName = data[1];

      if(this.skillName != "" && this.locationName == ""){

         _.each(this.skillList, function (o) {
          if(o.name == self.skillName)
            self.skillId = o.id;
         });

         this.searchList = {
          skills: [{'skillId': this.skillId}]
         }

      }

      if(this.locationName != "" && this.skillName == "") {

        _.each(this.locationList, function (o) { 
        if(o.city == self.locationName)
          self.locationId = o.id;
       });

       this.searchList = {
         city: [this.locationId]
       }

      }

      if(this.skillName != "" && this.locationName != ""){

        _.each(this.skillList, function (o) {
          if(o.name == self.skillName)
            self.skillId = o.id;
        });

        _.each(this.locationList, function (o) { 
          if(o.city == self.locationName)
            self.locationId = o.id;
        });

        this.searchList = {
          city: [this.locationId],
          skills: [{'skillId': this.skillId}]
        }

        

      }

      if(this.skillName == "" && this.locationName == ""){
        this.isJobAvailable = false;
        this.isJobLoaded = true;
      }

      this.fetchJobData();

    }

    else {
      this.isJobAvailable = false;
      this.isJobLoaded = true;
      this.isParamsAvailable = false;
    }

  }

  fetchJobData(){

    this.jobOpeningService.JobOpeningSearchAsync(this.searchList).subscribe(res => {

      this.jobData = res;
      console.log(this.jobData);
      //console.dir(this.jobData);

      if(this.jobData.length>0){

        this.isJobAvailable = true;
        this.isJobLoaded = true;

        if(!this.isMobile){
          this.selectedJobId = this.jobData[0].id;
          this.selectedJobDetails = this.jobData[0];
          this.jobDetailsChanged.emit(this.selectedJobDetails);
        }

        this.totalItems = this.jobData.length;  

      }
      else {
        this.isJobAvailable = false;
        this.isJobLoaded = true;

        this.totalItems = 0;
      }


    },
    error => {
      this.isJobAvailable = false;
      this.isJobLoaded = true;
    });

  }


  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;

    if(!this.isMobile){
      this.selectedJobId = this.jobData[this.ItemStartIndex].id;
      this.selectedJobDetails = this.jobData[this.ItemStartIndex];
    }

  }

  getFilterParams(event){
    this.filterParam = event;
  }


  isSelected(id){
    return this.selectedJobId == id ? 'selected' : '';
  }

  showJobDescription(job){
    this.selectedJobId = job.id;
    this.selectedJobDetails = job;

    this.jobDetailsChanged.emit(this.selectedJobDetails);


  }


  showJobDetaislModal(){

    const jobDialogRef = this.dialog.open(JobDetailSheetComponent, {
        panelClass: 'material',
        disableClose: true,
        data: this.selectedJobDetails
    });

  }

  @HostListener('window:resize', ['$event'])
    onResize(event){

      if(event.target.innerWidth <= 991)
      this.isMobile = true;
      else
      this.isMobile = false;

  }

  ngOnInit() {

    if(window.innerWidth <= 991)
      this.isMobile = true;
    else
      this.isMobile = false;


    this.jobDetailsChanged.subscribe( jobDetails => {

      if(this.isMobile){
        this.showJobDetaislModal();
      }

    })




  }


}
