import { Component, OnInit, Optional, Input, ViewChild, ElementRef, HostListener, Renderer2 } from '@angular/core';
import { Router, Event, NavigationEnd, ActivatedRoute } from '@angular/router';

import { MappingService } from '../../.././api/services/mapping.service';
import { SharedService } from '../../.././services/shared.service';

import * as _ from 'underscore';


@Component({
  selector: 'app-search-jobs-fields',
  templateUrl: './search-jobs-fields.component.html',
  styleUrls: ['./search-jobs-fields.component.scss']
})
export class SearchJobsFieldsComponent implements OnInit {

  @Input('skillName') jobSkill="";
  @Input('locationName') jobLocation="";

  @Input() type:any;

  skillList:any;
  locationList:any;

  skillId:any;
  locationId:any;

  jobElement:any;
  jobSearchDropdown:any;

  locationElement:any;
  locationSearchDropdown:any;
 

  @ViewChild ('searchJobsMini') jobInputElement;
  @ViewChild ('searchJobocationMini') locationInputElement;

  constructor(
    private mappingService: MappingService,
    private sharedService:SharedService,
    public router: Router,
    private route: ActivatedRoute,
    public element:ElementRef,
    public renderer:Renderer2
    ) { }



  submitJobsMiniForm(form) {

      var params;

      if(this.jobSkill != "" && this.jobLocation == ""){
        params = this.jobSkill+'&';
      }
      if(this.jobLocation != "" && this.jobSkill == ""){
        var splitLocation = this.jobLocation.replace(/ /g,'').split(',');
        params = '&'+splitLocation[0]+', '+splitLocation[1];
      }
      else if(this.jobSkill != "" && this.jobLocation != "") {
        var splitLocation = this.jobLocation.replace(/ /g,'').split(',');
        params = this.jobSkill+'&'+splitLocation[0]+', '+splitLocation[1];
      }
      else if(this.jobSkill == "" && this.jobLocation == "") {
        this.router.navigate(['/jobsearch']);
      }

      this.router.navigate(['/jobsearch', params]);  
  }

 

  @HostListener('document:click', ['$event']) 
    onDocumentClick(event) {
      
      const selectClass = this.jobSearchDropdown.classList.contains('show');

      if (!this.jobElement.contains(event.target)) {

        if(selectClass) {
          this.renderer.removeClass(this.jobSearchDropdown,'show');
        }
       
      }
  }

  getSkill(name, id){
    this.jobSkill = name;
    this.skillId = id;
  }

  showJobSkillData() {
   if(this.jobSkill.length > 1)
     this.renderer.addClass(this.jobSearchDropdown,'show');
   else
     this.renderer.removeClass(this.jobSearchDropdown,'show');

   this.skillId = null;

  }

  ngOnInit() {


    this.jobElement = this.jobInputElement.nativeElement;
    this.jobSearchDropdown = this.jobElement.nextSibling.nextSibling;

    this.locationElement = this.locationInputElement.nativeElement;
    this.locationSearchDropdown = this.locationElement.nextSibling.nextSibling;

    this.sharedService.skillsetdatacast.subscribe(data => {
      this.skillList = data;
    });

    this.sharedService.locationsetdatacast.subscribe(data => {
      this.locationList = data;
    });

  }

}
