import { Component, OnInit, HostListener, EventEmitter, ViewEncapsulation } from '@angular/core';

import { MatDialog } from '@angular/material';
import { MatDialogRef } from "@angular/material";

import { AuthService } from '../.././services/auth.service';
import { SharedService } from '../.././services/shared.service';

import * as _ from 'underscore';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss'],
  providers: [
    { provide: MatDialogRef, useValue: {} },
  ]
})
export class ProfileComponent implements OnInit {

  user:any;
  myJobs:any;
  myHotlist:any;

  selectedJobId:number = -1;
  selectedJobDetails:any;

  isJobName:boolean = false;
  profileLink:any;

  jobDetailsChanged: EventEmitter<any> = new EventEmitter();

  constructor(
  	private authService: AuthService,
    private sharedService:SharedService
  ) { }

  isSelected(id){
    return this.selectedJobId == id ? 'selected' : '';
  }

  showJobDescription(job){
    if(this.selectedJobId != job.id){
      this.selectedJobId = job.id;
    }
    else {
      this.selectedJobId = -1;
    }
    this.selectedJobDetails = job;
    this.jobDetailsChanged.emit(this.selectedJobDetails);


  }

  ngOnInit() {

    this.profileLink = window.location.href;

  	this.authService.userdetailscast.subscribe(user => {
  	  if(!_.isEmpty(user)){
  	  	this.user = user;
  	  }
      
    });

    this.sharedService.getJsonData().subscribe((res : any)=>{

      this.myJobs = res.myJobs;
      this.myHotlist = res.myHotList;
      

    },
    error => {
      console.log(error);
    }
    );

  }

}
